h4 {
  color: aquamarine;
  font-size: 20pt;
  margin-top: 10%;
}

h2 {
  font-size: 35pt;
}

p {
  color: grey;
  font-size: 14pt;
}

.Hori {
  align-items: flex-start;
  font-size: 20pt;
}
dd {
  color: grey;
  left: 100%;
  margin-top: 12%;
}

.Services {
  display: flex;
  list-style: none;
  justify-content: space-around;
  gap: 5px;
}

.buu {
  background-color: transparent;
  border: 2px solid #ffffff; /* Set border width and color */
  color: #ffffff; /* Set text color */
  padding: 10px 20px; /* Adjust padding as needed */
  font-size: 16px; /* Set font size */
  cursor: pointer;
  border-radius: 9px;
  text-align: center;
  transition: transform 0.3s ease-in-out;
  width: 200px;
  height: 130px;

}

.buu:hover {
  border: 4px solid aquamarine;
  background-color: transparent; /* Change background color on hover */
  color: aquamarine; /* Change text color on hover */
  font-size: 20px;
}
